.hidden {
	position: absolute;
	top: -99999999px;
	opacity: 0;
}

.Join {
	width: 100%;
	padding: 50px 0;
	margin: 0 auto;
	background: rgb(29, 29, 29);
	padding-top: 80px;

	> .inner {
		width: 1180px;
		margin: 0 auto;
		margin-top: 50px;
		h1 {
			width: 100%;
		}
		display: flex;
		flex-wrap: wrap;
		form {
			height: 900px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: rgb(32, 32, 32);
			width: 60%;
			fieldset {
				border: none;
				table {
					margin: 0 auto;
					th,
					td {
						color: white;
						padding: 10px 25px;
					}
					th {
						width: 30%;

						input[type='reset'],
						input[type='submit'] {
							width: 30%;
							height: 40px;
							margin: 10px;
							font: bold 14px/40px 'arial';
							color: #555;
							cursor: pointer;
							transition: 0.5s;
							border: none;

							&:hover {
								background: #333;
								color: #fff;
							}
						}
					}
					td {
						width: 70%;
						// border-left: 1px solid gray;
						input[type='text'],
						input[type='password'],
						select {
							color: white;
							width: 80%;
							background: rgba(0, 0, 0, 0);
							padding: 10px;
							border-right: 0px;
							border-top: 0px;
							border-left: 0px;
							border-bottom: 1px solid #666;
						}
						input[type='radio'],
						input[type='checkbox'] {
							margin-right: 30px;
						}

						textarea {
							resize: none;
							width: 80%;
							height: 150px;
							padding: 10px;
							background: rgba(0, 0, 0, 0.03);
							border: 1px solid #aaa;
							color: white;
						}
						.err {
							display: block;
							width: 100%;
							font: 11px/0.5 '맑은 고딕';
							@include red;
							margin-top: 5px;
						}
						:focus {
							outline: 1px solid;
						}
					}
				}
			}
		}
		.help {
			width: 40%;
			background: rgba(16, 16, 16, 0.3);
			text-align: center;
			display: flex;
			justify-content: center;
			flex-direction: column;
			h1 {
				font: 60px/1 'inter';
				margin-bottom: 50px;
				@include red;
			}
			p {
				color: white;
				width: 70%;
				margin: 0 auto;
				margin-bottom: 50px;
				font: 25px/1 'inter';
			}
			span {
				font: 20px/2 'roboto';
				color: #666;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.Join {
		width: 100%;
		.inner {
			width: 100%;
			padding: 20px 20px;
			display: flex;
			flex-direction: column;
			align-items: center;
			form {
				width: 100%;
			}
			.help {
				margin-top: 40px;
				padding: 40px 0;
				width: 100%;
			}
		}
	}

	@media screen and (max-width: $mobile) {
		.join {
			height: 1900px;
			.sub_visual {
				height: 200px;
			}
			.inner {
				width: 100%;
				padding: 20px 20px;

				.wrap {
					width: 100%;
					h1 {
						font-size: 40px;
					}
				}
			}
		}
	}
	.Join > .inner form fieldset table td input[type='text'],
	.Join > .inner form fieldset table td input[type='password'],
	.Join > .inner form fieldset table td select {
		width: 90%;
	}
}
