#visual {
	padding-top: 80px;

	width: 100%;
	height: 680px;
	background: rgb(16, 16, 16);
	.inner {
		width: 1180px;
		margin: 0 auto;

		.wrap {
			width: 100%;
			article {
				display: flex;
				.pic {
					width: 80%;
					margin-right: 40px;
					background: url(../../../public/img/IMG_0116.PNG) no-repeat center/cover;

					float: left;
					height: 600px;
				}
				.hello {
					display: flex;
					flex-direction: column;

					justify-content: center;
					h1 {
						font: bold 60px/1 'inter';
						padding-bottom: 20px;
						color: white;
					}
					h2 {
						font: 30px/1 'inter';
						@include red;
						font-size: 20px;
						padding-bottom: 10px;
					}
					p {
						// padding: 10px;
						color: #999;
						padding-bottom: 40px;
						line-height: 30px;
						opacity: 0.6;
						font-family: 'roboto';
					}
					a {
						display: block;
						height: 50x;
						width: 250px;
						@include button;
						float: left;
						color: white;
						font: bold 15px/50px 'inter';
						text-align: center;
						&:hover {
							@include hobutton;
						}
					}
				}
			}
		}
	}
}
@media screen and (max-width: $tablet) {
	#visual {
		height: 500px;
		.inner {
			width: 100%;
			.wrap {
				article {
					.pic {
						margin-left: 40px;
						height: 420px;
						width: 60%;
					}
					.hello {
						h1 {
							font-size: 40px;
						}
						h2 {
							font-size: 20px;
						}
						p {
							font-size: 15px;
							margin-right: 40px;
						}
					}
				}
			}
		}
	}
}
@media screen and (max-width: $mobile) {
	#visual {
		height: 600px;
		.inner {
			.wrap {
				article {
					padding-top: 20px;
					padding-left: 40px;
					padding-right: 40px;
					text-align: center;
					.pic {
						display: none;
					}
					.hello {
						p {
							margin-right: 0;
						}
						a {
							align-items: center;
							margin: 0 auto;
						}
					}
				}
			}
		}
	}
}
